import { HeaderItemCategoriesModel, HeaderSectionModel } from 'api/header/models/HeaderModel';
import HeaderBanner from 'components/HeaderBanner';

import routes from 'helpers/routes';
import Link from 'next/link';
import { useRouter } from 'next/router';
import React from 'react';

import styles from './NavbarContainer.module.scss';
import { useSelector } from 'react-redux';
import { SuisseIntl } from 'helpers/utils/fonts';
import useIsBigDesktop from 'helpers/hooks/useIsBigDesktop';

type NavbarContainer = {
	navArray: {
		categories: HeaderItemCategoriesModel[] | [];
		section: Record<string, HeaderSectionModel[]> | [];
		banner?: {
			bannerImg: string;
			text: string;
			link: string;
		};
	};
	category?: string;
	isBunnerMove: boolean;
	onMouseEnter: () => void;
	onMouseLeave: () => void;
	closeMobileNavbar?: () => void;
};

const NavbarContainer = ({
	navArray,
	isBunnerMove,
	onMouseEnter,
	onMouseLeave,
	closeMobileNavbar,
	category,
}: NavbarContainer) => {
	const router = useRouter();

	const isBigDesktop = useIsBigDesktop();

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const token = useSelector((state: any) => state.userData.token);
	const activeNav = useSelector((state: any) => state.isMobile.activeNav);

	const catalogUrl = routes.catalog + category;

	const closeMobileNavbarHandler = () => {
		if (closeMobileNavbar) closeMobileNavbar();
		onMouseLeave();
	};

	const COLSIZE = isMobile
		? ''
		: `calc((${isBigDesktop ? '1360px' : '100vw'} - ${document.documentElement.scrollWidth < 1188 ? '0px' : '500px'}) / (${
				Object.keys(navArray.section).length
		  } + 1 + ${navArray.categories.length ? 1 : 0}))`;
	const COLSIZEDOUBLE = isMobile
		? ''
		: `calc((${isBigDesktop ? '1360px' : '100vw'} - ${document.documentElement.scrollWidth < 1188 ? '0px' : '500px'}) / (${
				Object.keys(navArray.section).length
		  } + 1 + ${navArray.categories.length ? 1 : 0}) * 2)`;

	return (
		<div onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} className={styles.navbarContainer}>
			<div className={styles.navbarContainerBlock}>
				{navArray.categories && Array.isArray(navArray.categories) && (
					<div className={styles.categories}>
						{catalogUrl.includes('deti') && (
							<Link
								className={isMobile ? SuisseIntl.className : ''}
								href={routes.aboutKids}
								onClick={closeMobileNavbarHandler}
							>
								О бренде "TOTTI Kids"
							</Link>
						)}
						{isMobile && (
							<Link
								className={isMobile ? SuisseIntl.className : ''}
								href={catalogUrl}
								onClick={closeMobileNavbarHandler}
							>
								Смотреть все
							</Link>
						)}
						{navArray.categories.map(item => (
							<Link
								className={isMobile ? SuisseIntl.className : ''}
								key={item.name}
								href={routes.catalog + item.code}
								style={{
									width: navArray.categories.length > 7 && !isMobile ? COLSIZEDOUBLE : COLSIZE,
									color: item.name === 'Sale' ? '#FF3A3A' : '',
								}}
								onClick={closeMobileNavbarHandler}
							>
								{item.name}
							</Link>
						))}
					</div>
				)}
				<div className={styles.section}>
					{navArray.section &&
						Object.keys(navArray.section).map(item => {
							return (
								<div
									key={item}
									className={styles.sectionContainer}
									style={{ width: navArray.section[item].length > 8 && !isMobile ? COLSIZEDOUBLE : COLSIZE }}
								>
									<Link
										href={routes.catalog + item.split(':')[1]}
										className={`${styles.sectionTitle} ${isMobile ? SuisseIntl.className : ''}`}
										onClick={closeMobileNavbarHandler}
									>
										{item.split(':')[0]}
									</Link>
									<div className={styles.sectionCol}>
										{navArray.section[item].map(sectionItem => {
											return (
												<Link
													className={styles.sectionItem}
													href={routes.catalog + sectionItem.href}
													key={sectionItem.name}
													onClick={closeMobileNavbarHandler}
												>
													{sectionItem.name}
												</Link>
											);
										})}
									</div>
								</div>
							);
						})}
				</div>
				{navArray.banner && navArray.banner.bannerImg && (
					<HeaderBanner
						className={isBunnerMove && !isMobile ? styles.isBunnerMove : ''}
						img={navArray.banner.bannerImg}
						text={navArray.banner.text}
						link={navArray.banner.link || ''}
					/>
				)}
				{isMobile && (
					<div className={styles.lkAndTranslate}>
						<Link
							href={
								router.pathname +
								(token ? routes.lk : routes.signIn) +
								(Object.keys(router.query).length !== 0
									? Object.entries(router.query)
											.map(item => `&${item[0]}=${item[1]}`)
											.join('')
									: '')
							}
							shallow
						>
							<button className={SuisseIntl.className}>Личный кабинет</button>
						</Link>
						{/*<Link href={router.asPath} locale={changeTo}>
							<button className={SuisseIntl.className}>{t('header.local')}</button>
						</Link>*/}
					</div>
				)}
			</div>
		</div>
	);
};

export default React.memo(NavbarContainer);
